@import '../style/variables.scss';

.btn {
  background: $red-color-100;
  border: 1px solid $red-color-100;
  border-radius: 6px;
  box-shadow: rgba(0, 0, 0, 0.1) 1px 2px 4px;
  box-sizing: border-box;
  color: #FFFFFF;
  cursor: pointer;
  display: inline-block;
  font-family: nunito,roboto,proxima-nova,"proxima nova",sans-serif;
  font-size: 16px;
  font-weight: 800;
  line-height: 16px;
  min-height: 40px;
  outline: 0;
  padding: 12px 14px;
  text-align: center;
  text-rendering: geometricprecision;
  text-transform: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: middle;
  &:hover,
  &:active {
	 background-color: initial;
  	background-position: 0 0;
  	color: $red-color-100;
  }
  &::active {
  	opacity: .5;
	}
}

.btn-white {
  border: 1px solid $white-color;
  background-color: initial;
  &:hover {
    background-color: $black-color-300;
    color: $white-color;
  }
  &:active {
     background-color: $red-color-100;
  }
}

.play__button {
  margin: 0 auto;
  top: 22%;
  left: 5%;
  position: relative;  
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 1.5em 0 1.5em 2em;
  border-color: transparent transparent transparent $white-color;
  opacity: .75; 
  &_outer {
    border-radius: 50%;
    width: 5em;
    height: 5em;
    background-color: $red-color-100;
    cursor: pointer;
    &:hover {
      background-color: $black-color-100;
    }
    &:active {
      background-color: $black-color-300;
    }
    &:hover .play-button {
      opacity: 1;
    }
  }
}

